import React from 'react';

class Projects extends React.Component {
    constructor( props ) {
        super( props );
        this.props = props;
        this.state = {
            content: ''
        }
    }

    componentDidUpdate() {
        ( this.state.content === '' ) ?
            this.setState( { content: this.props.content } ) : console.log( 'Child already has state' );
    }

    render() {
        let windowContent = 'Loading...';
        if ( this.state.content !== '' ) {
            windowContent = this.state.content.content.map( ( item, key ) => (
                <>
                    <tr className="projects-table__header">
                        <th colspan="3">
                            <a className="projects-link" href={this.state.content.url[key]}><p className="projects-link">{this.state.content.header[key]}</p></a>
                        </th>
                    </tr>
                    <tr>
                        <td>
                            <img className="projects-table__image" src={this.state.content.image[key]} />
                        </td>
                        <td className="projects-table__center">{item.split( "|" ).map( ( elem, key ) => (
                            <p className="elem">{elem}</p>
                        ) )}
                        </td>
                        <td>{this.state.content.languages[key]}</td>
                    </tr>
                </>
            ) )
        }

        return (
            <>
                <h4 class="content__title">{this.state.content['title']}</h4>
                {/* <p>{this.state.content['content']}</p> */}
                <table className="projects-table">
                    {windowContent}
                </table>
            </>
        );
    }
}

export default Projects;