import React from 'react';

class Contact extends React.Component {
    constructor( props ) {
        super( props );
        this.props = props;
        this.state = {
            content: ''
        }
    }

    componentDidUpdate() {
        ( this.state.content === '' ) ?
            this.setState( { content: this.props.content } ) : console.log( 'Child already has state' );
    }

    render() {
        let windowContent = 'Loading...';
        if ( this.state.content !== '' ) {
            windowContent = this.state.content.content.map( ( item, key ) => (
                <tr>
                    <td>
                        <p key={key}>{item}</p>
                    </td>
                    <td>
                        <a className="contact-link" href={this.state.content.url[key]} > LINK</a>
                    </td>
                </tr>
            ) )
        }

        return (
            <>
                <h4 className="content__title">{this.state.content['title']}</h4>
                <table>
                    {windowContent}
                </table>
            </>
        );
    }
}

export default Contact;